//avtivity-card
.avtivity-card{
	position:relative;
	z-index:1;
	overflow:hidden;
	.activity-icon{
		height:80px;
		width:80px;
		min-width:80px;
		display:block;
		@include transitionMedium;
		border-radius:100%;
		line-height:80px;
		text-align:center;
	}
	.title{
		font-size:28px;
	}
	.progress{
		position:absolute;
		bottom:0;
		left:0;
		width: 100%;
	}
	.effect{
		position: absolute;
		display: block;
		width: 0;
		height: 0;
		border-radius: 50%;
		transition: width .4s ease-in-out,height .4s ease-in-out;
		-webkit-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
		z-index: -1;
	}
	p,.media-body span{
		@include transitionMedium;
	}
	&:hover {
		.activity-icon{
			background:$white!important;
		}
		.effect{
			width: 225%;
			height: 562px;
		}
		p{
			color:$white;
		}
		.media-body{
			span{
				color:$white!important;
			}
		}
		.progress{
			background:rgba($white,0.2)!important;
			.progress-bar{
				background:$white !important;
			}
		}
	}
	@include respond ('phone-land'){
		.title{
			font-size:20px;
		}
		.activity-icon{
			height:65px;
			width:65px;
			min-width:65px;
			line-height:65px;
			svg{
				width:35px;
				height:35px;
			}
		}
	}
}

//testimonial-one
.testimonial-one{
	.card{
		box-shadow:none;
		@include transitionMedium;
		margin-bottom:0;
		img{
			height:113px;
			width:113px;
			margin-left:auto;
			margin-bottom: 15px;
			margin-right:auto;
			border-radius:100%;
		}
		
	}
	.owl-nav{
		.owl-prev,.owl-next{
			height:45px;
			width:45px;
			cursor:pointer;
			line-height:45px;
			border-radius:100%;
			position:absolute;
			top:40%;
			background:$white!important;
			color:$primary!important;
			text-align:center;
			font-size:18px!important;
			box-shadow:0px 0px 10px rgba(0,0,0,0.1);
			
			&:hover{
				background-color:$primary!important;
				color:$white!important;
				transition:all 0.5s;
			}
		}
		.owl-next{
			right:-51px;
		}
		.owl-prev{
			left:-51px;
		}
		@include respond ('phone'){
			.owl-next,.owl-prev{
				height:50px;
				width:50px;
				line-height:50px;
				font-size:20px;
			}
			.owl-next{
				right:-10px;
			}
			.owl-prev{
				left:-10px;
			}
		}
	}
}
.owl-carousel .owl-nav.disabled{
	display:block;
}

